header{
  height: 130vh;
  padding-top: 1rem;
  /*background-color: var(--color-bg-variant);*/
  /*background-color: var(--color-bg);*/
  overflow: hidden;
  background: linear-gradient(90deg, var(--color-primary) 0.97%, var(--color-bg));
}

section#home{
  padding-top: 2rem;
}

.header__container{
  position: relative;
  height: 100%;
  font-variant: small-caps;
  color: white;
  text-align: left;
  margin: 0 auto;
  width: var(--container-width-lg);

  
}

.header__container h2{
  font-size: 25px;
  margin-left: -1.8rem;
}

/* BTN */
.buttons{
  display: flex;
  font-weight: 300;
  color: var(--color-dark);
  text-transform: uppercase;
  justify-content:center;
  margin-left: 13px;
}



/*IMAGE*/

.image__me{
  width: 20rem;
  height: 20rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: -27.2rem;
  overflow: hidden;
  border-radius: 0px 0px 0px 120px;
  box-shadow: 28px -28px var(--color-yellow);
  z-index: 1;
  /* position: absolute;
  width: 20rem;
  height: 20rem;
  left: calc(52% - 10rem);
  margin-top: 4rem;
  
  box-shadow: 28px -28px var(--color-yellow);
  z-index: 1;
  overflow: hidden;*/
  

}

h5.subtitle__intro{
  width: 50%;
  background:  linear-gradient(90deg, #254d5e6e 0.97%, #eae7de52);
  font-family: var(--ff-secondary);
  font-weight: lighter;
  font-variant: small-caps;
  letter-spacing: 8px;
  font-size: 2em;
  height: 4rem;
  margin-top: 10px;
  color: #fff;
  display: inline-block;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.136);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  z-index: -1;
  align-self: start;
  text-align: left;
}

/*CIRCLE DESIGN - HOME*/

#home-outer-circle{
  position: absolute;
  border: 50px solid #f7faf92f;
  border-right: 50px solid #b2c0be32;
  border-bottom: 50px solid #b2c0be47;
  border-top: 50px solid #b2c0be3e;
  /*box-shadow: 0px 0px 0px 2px rgba(249, 249, 250, 0.234);*/
  border-radius: 50%;
  height: 370px;
  width: 370px;
  left: 310px;
  top: 150px;
 
}

#home-inner-circle{
  position: absolute;
   border: 3px dotted rgba(249, 249, 250, 0.41);
  border-radius: 50%;
  height: 225px;
  width: 230px;
  top: 375px;
  left: 530px;
  margin: -150px 0px 0px -150px;  
  
}

